}\n\t},\n\tcomputed: {\n\t\t...mapGetters([\n\t\t\t'tasks',\n\t\t]),\n\t},\n\tbeforeMount() {\n\t\tthis.tasksUpdater = setInterval(() => this.getTasks(true), 5000)\n\t},\n\tbeforeDestroy() {\n\t\tclearInterval(this.tasksUpdater)\n\t},\n\tmethods: {\n\t\t...mapActions([\n\t\t\t'getTasks',\n\t\t]),\n\t},\n}\n</script>\n\n<style scoped>\n.list-enter-active,\n.list-leave-active {\n\ttransition: all 1s ease;\n}\n\n.list-enter-from,\n.list-leave-to {\n\topacity: 0;\n\ttransform: translateY(30px);\n}\n\n.tasks-list {\n\tpadding: 20px;\n\tbackground-color: var(--color-primary-element-light);\n\tborder-radius: var(--border-radius-large);\n\twidth: 100%;\n\tmargin: 10px;\n\tmax-width: 600px;\n\tmax-height: 482px;\n\tmin-height: 410px;\n\toverflow-y: scroll;\n}\n\n.task-row {\n\tdisplay: flex;\n\talign-items: center;\n\tmargin-bottom: 10px;\n\ttransition: box-shadow .3s;\n\tborder-radius: var(--border-radius-pill);\n\tborder-color: var(--color-border-dark);\n}\n\n.task-row:hover {\n\tbox-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.1);\n}\n\n@media (max-width: 540px) {\n\t.task-row, .task-row a {\n\t\tflex-direction: column;\n\t\tpadding: 5px;\n\t}\n\t.delete-task-button {\n\t\tvisibility: visible;\n\t}\n}\n\n.empty-tasks-list {\n\tpadding: 20px;\n\twidth: 100%;\n\theight: 80%;\n\tdisplay: flex;\n\talign-items: center;\n\tjustify-content: center;\n\tbox-sizing: border-box;\n\tflex-direction: column;\n}\n</style>\n"],"sourceRoot":""}]);
